<template>
     <div class="modal fade in" id="hospitalProductsInfo" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" style="display: none;">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                        <h4 class="modal-title" id="exampleModalLabel"><span v-text="$parent.$data.currentHospital"></span>-申请产品</h4>
                    </div>
                    <div class="modal-body forms">
                        <div class="form-inline">
                            <div class="row">
                                <div class="col-sm-2">
                                    <label class="control-label">申请单位</label>
                                    <span class="require-label">*</span>
                                </div>
                                <div class="col-sm-4">
                                    <select v-validate="'required'" name="units" class="form-control" required="" id="units" v-model="$parent.$data.applyModel.partment.value" v-bind:class="{'haserror': errors.has('units') }">
                                        <option value="">请选择申请单位</option>
                                        <option v-for="item in $parent.$data.applyProduct.units" v-bind:value="item.id" v-text="item.name"></option>
                                    </select>
                                </div>
                                <div class="col-sm-2">
                                    <label class="control-label">产品</label>
                                    <span class="require-label">*</span>
                                </div>
                                <div class="col-sm-4">
                                    <select v-validate="'required'" name="product" class="form-control" required="" id="products" v-on:change="applyProductBind" v-model="$parent.$data.applyModel.product.value" v-bind:class="{'haserror': errors.has('product') }">
                                        <option value="">请选择产品</option>
                                        <option v-for="product in $parent.$data.applyProduct.products" v-bind:value="product.id" v-text="product.name"></option>
                                    </select>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-sm-2">
                                    <label class="control-label">使用中</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="checkbox" id="isActive" checked="" v-model="$parent.$data.applyModel.use"></div>
                                <div class="col-sm-2">
                                    <label class="control-label"></label>
                                </div>
                                <div class="col-sm-4" v-show="$parent.$data.applyModel.use">
                                    已申请</div>
                            </div>
                            <div class="row">
                                <div class="col-sm-2">
                                    <label class="control-label">别名</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="text" id="alias" class="form-control" placeholder="别名" maxlength="50" v-model="$parent.$data.applyModel.otherName"></div>
                                <div class="col-sm-2">
                                    <label class="control-label">分类</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="text" id="category" class="form-control" placeholder="分类" maxlength="30" required="" v-model="$parent.$data.applyModel.className"></div>
                            </div>
                            <div class="row">
                                <div class="col-sm-2">
                                    <label class="control-label">检验入库</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="checkbox" id="needCheck" v-model="$parent.$data.applyModel.needCheck"></div>
                                <div class="col-sm-2">
                                    <label class="control-label">分装</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="checkbox" id="needSplit" v-model="$parent.$data.applyModel.needSplit"></div>
                            </div>
                            <div class="row">
                                <div class="col-sm-2">
                                    <label class="control-label">分装份数</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="text" id="splitCopies" class="form-control" placeholder="分装份数" maxlength="10" v-model="$parent.$data.applyModel.splitCopies"></div>
                                <div class="col-sm-2">
                                    <label class="control-label">分装单位</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="text" id="splitUnit" class="form-control" placeholder="分装单位" maxlength="10" v-model="$parent.$data.applyModel.splitUnit"></div>
                            </div>
                            <div class="row">
                                <div class="col-sm-2">
                                    <label class="control-label">分装包装容量</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="text" id="splitCapacity" class="form-control" placeholder="分装包装容量" maxlength="10" v-model="$parent.$data.applyModel.splitCapacity"></div>
                                <div class="col-sm-2">
                                    <label class="control-label">分装包装数量</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="text" id="miniSplitNumber" class="form-control" placeholder="分装包装数量" maxlength="10" v-model="$parent.$data.applyModel.miniSplitNumber"></div>
                            </div>
                            <div class="row">
                                <div class="col-sm-2">
                                    <label class="control-label">赠送数量</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="text" id="donateCount" class="form-control" placeholder="赠送数量" maxlength="10" v-model="$parent.$data.applyModel.donateCount"></div>
                                <div class="col-sm-2">
                                    <label class="control-label">赠送基数</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="text" id="donateBase" class="form-control" placeholder="赠送基数" maxlength="10" v-model="$parent.$data.applyModel.donateBase"></div>
                            </div>
                            <div class="row">
                                <div class="col-sm-2">
                                    <label class="control-label">有效天数</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="text" id="validDays" class="form-control" placeholder="有效天数" maxlength="5" v-model="$parent.$data.applyModel.validDays"></div>
                                <div class="col-sm-2">
                                    <label class="control-label">到货天数</label>
                                </div>
                                <div class="col-sm-4">
                                    <input type="text" id="arrivalDays" class="form-control" placeholder="到货天数" maxlength="5" v-model="$parent.$data.applyModel.arrivalDays"></div>
                            </div>
                            <div class="row">
                                <div class="col-sm-2">
                                    <label class="control-label">定价</label>
                                    <span class="require-label">*</span>
                                </div>
                                <div class="col-sm-4">
                                    <input v-validate="'required'" name="price" type="text" id="price" class="form-control" placeholder="定价" maxlength="15" required="" v-model="$parent.$data.applyModel.price.value" v-bind:class="{'haserror': errors.has('price')}"></div>
                                <div class="col-sm-2">
                                    <label class="control-label">大包装定价</label>
                                </div>
                                <div class="col-sm-4">
                                    <p id="packagePrice" class="form-control-static"></p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <label id="error" class="label-danger"><span v-show="errors.has('units')||errors.has('product')||errors.has('price')" class="help is-danger">请选择红框中的必填项</span></label>
                        <button type="button" class="btn btn-primary" id="btnConfirm" @click="submitApply">确认</button>
                        <button type="button" class="btn btn-default" data-dismiss="modal" id="btnClose">关闭</button>
                    </div>
                </div>
            </div>
        </div>
</template>

<script>
export default {
  name: 'applyProduct',
  data () {
    return {             
        applyProductInfo:{
            inUse:true,
            otherName:"",
            className:""
          },
    }
  },

  methods:{
        //根据产品选择获取产品绑定信息
    applyProductBind(){
        var _this = this;
        var url = this.GLOBAL.hostIp;
        var val = $(event.currentTarget).val();     
        var UnitId =  _this.applyModel.partment.value
        this.$http.post(url+"/HospitalSetting/JsonGetHospitalProduct",{"unitId":UnitId,"productId":val},{emulateJSON: true,credentials: true}).then(response => {
            var body = response.body;
               if (body.isSuccess) {
                var data = body.data;
                   _this.applyModel.otherName = data.fullName;
                   _this.applyModel.className = data.category;
                   _this.applyModel.use = data.isUsing;
               };                
              }, response => {
                // error callback
              });
    },
    submitApply(){        
        //验证必填框是否填写      
        var _this = this.$parent.$data;
        var url = this.GLOBAL.hostIp;
       
         this.$validator.validateAll().then(() => {
        // eslint-disable-next-line
      var data = {
            HospitalId:_this.$parent.$data.applyProductHospitalId,
            UnitId: _this.applyModel.partment.value,
            ProductId:_this.applyModel.product.value,
            Category:_this.applyModel.className,
            Alias:_this.applyModel.otherName,
            NeedCheck:_this.applyModel.needCheck,
            NeedSplit:_this.applyModel.needSplit,
            SplitCopies:_this.applyModel.splitCopies,
            SplitUnit:_this.applyModel.splitUnit,
            SplitCapacity:_this.applyModel.splitCapacity,
            MiniSplitNumber:_this.applyModel.miniSplitNumber,
            DonateCount:_this.applyModel.donateCount,
            DonateBase:_this.applyModel.donateBase,
            ValidDays:_this.applyModel.validDays,
            ArrivalDays:_this.applyModel.arrivalDays,
            Price:_this.applyModel.price.value,
            PackagePrice:"",
            IsActive:true
         }         
         // 发送http请求
         this.$http.post(url+"/HospitalSetting/SaveHospitalProduct",data,{emulateJSON: true,credentials: true}).then(response => {
                
              }, response => {
                // error callback
              });
      $("#hospitalProductsInfo").modal("toggle")
      }).catch(() => {
        // eslint-disable-next-line
       
      });
         //
         
      
    },
  }
}
</script>
<style>  
</style>


